.content {
    grid-area: c;
    background-color: #eeadff;
    overflow: hidden;
}
.bg img {
    width: 100%;
    max-height: 300px;
    aspect-ratio: 1/1;
    filter: brightness(90%);

    transition: .3s;
}
.bg img:hover{
    filter: brightness(100%);
}

.item {
    color: green;
}